<template>

  <img  class="imgbox1"  :style="{transform:item.deg}" :src="item.url"   @click="imgc(index)"   alt="" />

</template>

<script type="text/ecmascript-6">
    export default {
          props : ["item","imgc","index"]
    }
</script>

<style scoped>
  .imgbox1{
    width:24.25%;
    max-height:74px;
    margin-right: 1%;
    transition: all ease 0.2s;
  }

</style>



