<template>
  <div class="lt_body">
      <form action="">
           <b-input :getsecodbtn="getsecodbtn" :gettext="gettext" :secod="secod"></b-input>
           <b-verification></b-verification>
           <b-btn :isgoto="isgoto"></b-btn>
            <b-prompt :errvalnone="errvalnone" :errvalue="errvalue"></b-prompt>
      </form>
  </div>
</template>

<script type="text/ecmascript-6">
    import  bInput from  "./bInput.vue";
    import  bVerification from  "./bVerification.vue";
    import  bBtn from  "./bBtn.vue";
    import  bPrompt from  "./bPrompt.vue";


    export default {
        props : ["gettext","isgoto","errvalue","secod","getsecodbtn","errvalnone"],
        components :  {
            bInput ,
            bVerification ,
            bBtn,
            bPrompt
        }
    }
</script>

<style scoped>
  .lt_body {
    padding: 0 0.27624rem; }
</style>



