<template>
        <div class="s_h">
                <div class="s_h_b1" @click="sshide"></div>
                <form action="" class="s_h_b2">
                      <input class="ins" type="text" placeholder="口红" @keyup.enter="sssave(msg,clearmsg)"  v-model.trim="msg">
                      <a href="javascript:;" class="s_h_b3" @click="sssave(msg,clearmsg)">搜索</a>
                </form>
        </div>
</template>

<script type="text/ecmascript-6">
    export default {
      props : ["sshide","sssave"],
      data (){
            return {
                    msg : ""
            }
      },
      methods : {
            clearmsg: function () {
                    this.msg = "";
            }
      }
    }
</script>

<style scoped>
  .s_h {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    width: 100%;
    height: 0.49724rem;
    border-bottom: 1px solid #eee;
    padding: 0.05525rem 0;
    box-sizing: border-box; }

  .s_h_b1 {
    left: 0.08287rem;
    top: 0.13812rem;
    width: 7%;
    height: 0.22099rem;
    color: #666;
    background: url(../../assets/img/goback.png) center center no-repeat;
    background-size: contain; }

  .s_h_b2 {
    display: flex;
    justify-content: space-between;
    width: 90%;
    height: 0.27624rem;
    left: 10%;
    top: 24%;
    -webkit-box-sizing: border-box; }

  .s_h_b2 > input {
    padding: 0.05525rem 0.1105rem;
    width: 100%;
    height: 0.16575rem;
    box-shadow: none;
    -webkit-appearance: none;
    border-radius: 0.04972rem;
    border: 1px solid #aaa;
    font-size: 0.1547rem;
    line-height: 0.27624rem;
    vertical-align: middle;
    background: transparent;
    background-color: #FFF;
    outline: none; }

  .s_h_b3 {
    display: inline-block;
    height: 0.27624rem;
    min-width: 0.33149rem;
    right: 0;
    font-size: 0.1547rem;
    line-height: 0.27624rem;
    text-align: center;
    color: #666666;
    margin-left: 0.1105rem; }
  .ins :focus {
       border: 1px solid pink !important;
  }
</style>



