<template>
  <div>
      <h></h>
      <bbody :changeimg="changeimg" :gettext="gettext"   :lists="lists" :imgc="imgc" :isgoto="isgoto" :errvalue="errvalue"></bbody>
  </div>
</template>

<script type="text/ecmascript-6">
    import h from  "../components/login/h.vue";
    import bbody from "../components/login/bbody.vue";
    import api  from "../../apis/pagapi";

    import "../assets/js/jq.cookie"
    export default {
      data(){
        return {
          lists : [
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(0deg)",zdeg : 0,istrue:true},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(90deg)",zdeg : 90,istrue:false},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(180deg)",zdeg : 180,istrue:false},
            {num : 1,url : "src/assets/img/login/1.png",deg : "rotate(0deg)",zdeg : 0,istrue:true}
          ]
          ,
          up : {
            u: "",
            p: ""
          }
          ,
          errvalue : {
            isture : false,
            val : ""
          }
          ,
          arrimgs : [],
          arrimg1 : []
        }
      }
      ,
      created : function () {
              this.changeimg();
      }
      ,
      mounted : function () {

      }
      ,
      methods : {
            //换一组图片
            changeimg(){
                  this.arrimgs = [];
                  this.arrimg1 = [];
                    //从20个图片中随机取出不重复的4个图片
                  while(true){
                          let sjnum = parseInt(Math.random()*12);
//                       退出条件
                          if(this.arrimg1.length == 4){
                               break;
                          }
//                       找
                          if(this.arrimg1.length === 0){
                                this.arrimg1.push(sjnum);
                                this.arrimgs[sjnum] = 21;
                          }
                          for(let i =0; i<13; i++){
                                 if(this.arrimgs[sjnum] !== 21 ){
                                   this.arrimgs[sjnum]=21;
                                   this.arrimg1.push(sjnum);
                                 }else{
                                      break;
                                 }
                          }
                  }
                  var imgurl = this.arrimg1;
//                  换图片
                  this.lists.forEach((item,i)=>{
                          var sj = parseInt(Math.random()*4);
//                          console.log(sj);
                          item.url =  `src/assets/img/login/${imgurl[i]}.png`;
                          item.deg = `rotate(${sj * 90}deg)`;
                          item.zdeg = `${sj * 90}`;
                  })
            }
            ,
            //每次点击旋转加值
            _rotateimg : function (i) {
                var a = this.lists[i];
                this.lists[i].zdeg = Number(a.zdeg) + 90;
                this.lists[i].deg = `rotate(${this.lists[i].zdeg}deg)`;
//                console.log(this.lists[i].zdeg);
//              console.log(this.lists[i].deg);
            }
            ,
            //每次点击后记录当前的状态
            _imgistrue : function () {
                this.lists.forEach((item,i)=>{
                        var a = item.zdeg % 360;
                        if(a==0){
                              item.istrue = true;
                        }
                        else{
                              item.istrue = false;
                        }
                })
            }
            ,
            //判断所有图片是否正向？
            _imgsrt :  function () {
                  var imgt = true;
                  var arr = this.lists;
                  for(let i =0;i<arr.length;i++){
                        var a = arr[i].istrue;
                        if(a===false){
                          return false;
                        }
                  }
                  return true;
            }
            ,
//         获取文本框的值
            gettext(u,p){
                  this.up.u = u;
                  this.up.p = p;
            }
            ,
      // 验证系统   验证 账号 密码  图片
          _vbup(){
                  var u = this.up.u.length;
                  var p = this.up.p.length;
                  var imgs =  this._imgsrt();
                  console.log("我是图片验证:"+imgs)
//                验证账号
                  if(u==""){
                    this.errvalue.val = "账号不能为空"
                    return false;
                  }else if(u>11){
                    this.errvalue.val = "账号不能超过11个字符"
                    return false;
                  }
//                 密码验证
                if(p==""){
                  this.errvalue.val = "密码不能为空"
                  return false;
                }else if(p>6){
                  this.errvalue.val = "密码不能超过6个字符"
                  return false;
                }
                if(imgs===false){
                  this.errvalue.val = "请保证4个图片全部为正方向"
                  return false;
                }
                return true;
          }
          ,
//       是否调用错误提示
          errval : function (a) {
                var tant = this;
                if(a=="1"){
                        this.errvalue.isture = true;
                        setTimeout(function () {
                            tant.errvalue.isture = false;
                        },1600);
                }else{
                       this.errvalue.isture = false ;
                }
          }

          ,
          //是否跳转按钮
            isgoto : function () {

                  //验证。。。。。
                  var a = this._vbup();
                  var tant = this;
                  if(a===true){
                        //跳转
                    console.log("3个验证通过，登录了。。。")
                      var url = `/login?user=${this.up.u}&pass=${this.up.p}`;
                      api.getpagapi(url,function (data) {
                                if(data.login == 1){
                                      tant.errvalue.val = "恭喜您，注册成功 ，2秒之后跳转到登陆首页";
                                      tant.errval("1");
                                      setcookie('username',tant.up.u, { expires: 7 });
//                                      setTimeout(function () {
//                                        location.href = "/#/login";
//                                      },2000)
                                }
                                else if(data.login == 2){
                                  tant.errvalue.val = "用户名不存在";
                                  tant.errval("1");
                                } else if(data.login == 3){
                                  tant.errvalue.val = "系统出错，请稍后再试";
                                  tant.errval("1");
                                }
                                else {
                                  tant.errvalue.val = "未知错误";
                                  tant.errval("1");
                                }
                      })

                  }else{
                       //调用提示系统
                        console.log("有错误，调用 提示模块 ~")
                        this.errval("1");
                  }
                  //防止settimeout
                  return;
            }
            ,
            imgc : function (i) {
                  //判断是否为正方向
                 this._rotateimg(i);
                 //每次点击记录
                  this._imgistrue();
            }


      }
      ,
        components : {
              h,
              bbody
        }
    }
</script>

<style scoped>

</style>



