<template>
      <div class="l_notdl">
        <div class="l_notdl_box">
          <p>其他登录方式</p>
          <div class="l_notdl_box_ul">
            <div @click="inull"><img src="src/assets/img/l_qq.png" alt="" /><span>QQ登录</span></div>
            <div @click="exemptLogin"><img src="src/assets/img/l_phone.png" alt="" /><span>免密登录</span></div>
            <div @click="login"><img src="src/assets/img/l_zhanghao.png" alt="" /><span>账号登录</span></div>
          </div>
        </div>
      </div>
</template>

<script type="text/ecmascript-6">
    export default {
          methods : {
                 exemptLogin : function () {
                      location.href = "/#/exemptLogin";
                 }
                 ,
                login : function () {
                  location.href = "/#/login";
                }
                ,
                inull : function () {
                  alert("该功能暂未开通");
                }
          }
    }
</script>

<style scoped>
  .l_notdl_box {
    margin-top: 0.82873rem; }
  .l_notdl_box > p {
    font-size: 0.16575rem;
    font-weight: 400;
    text-align: center;
    color: #999;
    margin: 0.44199rem 0 0.16575rem; }
  .l_notdl_box .l_notdl_box_ul {
    padding: 0.27624rem 0;
    display: flex;
    flex: 0 1 auto;
    flex-flow: row wrap;
    justify-content: space-around; }
  .l_notdl_box .l_notdl_box_ul > div {
    display: flex;
    flex-flow: column nowrap;
    position: relative;
    text-align: center;
    margin: 0 auto; }
  .l_notdl_box .l_notdl_box_ul > div > img {
    margin: 0 auto;
    width: 0.48619rem; }
  .l_notdl_box .l_notdl_box_ul > div > span {
    margin-top: 0.09945rem;
    font-size: 0.14365rem;
    color: #666; }

  .l_notdl_box:before {
    content: "";
    display: block;
    width: 0.22099rem;
    border-top: 1px solid #bbb;
    margin: 0 auto; }
</style>



