<template>
    <div class="bprompt" v-show="errvalue.isture">
            <div class="bprbox">
                    <p>{{ errvalue.val }}</p>
            </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
      props : ["errvalue"]
    }
</script>

<style scoped>
       .bprompt{
            width: 100%;
           position: fixed;
           left: 0;
           top: 0;
           right: 0;
           bottom: 0;
           background-color: transparent;
           z-index: 999;
       }
      .bprbox{
        width: 100%;
        height: 100%;
       display: flex;
        justify-content: center;
        align-items: center;

      }
       .bprbox  >p{
         min-width: 0.88398rem;
         max-width: 70%;
         word-wrap: break-word;
         padding: 0.1105rem 0.1326rem;
         font-size: 0.1547rem;
         color: #fff;
         line-height: 1.5;
         text-align: center;
         border-radius: 0.03315rem;
         background-color: rgba(225, 42, 82, 0.7);
       }

</style>



