<template>
      <div class="s_body">
        <sou-suo-body-b1 :historydata="historydata" :ishistory="ishistory" :ins="ins"></sou-suo-body-b1>
        <sou-suo-body-b2></sou-suo-body-b2>
      </div>
</template>

<script type="text/ecmascript-6">
  import  SouSuoBodyB1 from "./SouSuoBodyB1.vue"
  import  SouSuoBodyB2 from "./SouSuoBodyB2.vue"
    export default {
    props : ["ishistory","historydata","ins"],
            components : {
                  SouSuoBodyB1,
                  SouSuoBodyB2
            }
    }
</script>

<style scoped>
  .s_body {
    position: absolute;
    top: 0.49724rem;
    bottom: 0;
    right: 0;
    left: 0; }
</style>



