</template>

<script type="text/ecmascript-6">
    import headers from  "../components/loginnav/header.vue";
    import OtherLogin from  "../components/loginnav/OtherLogin.vue";
    import WeixinLogin from  "../components/loginnav/WeixinLogin.vue";
    export default {
          components : {
              headers,
              WeixinLogin,
              OtherLogin
          }
    }
</script>

<style scoped>
  .l_login {
    width: 84%;
    margin: 0 auto;
    padding-bottom: 0.55249rem; }
  .l_login img {
    width: 0.24862rem; }
</style>



