<template>
  <div>
    <rh></rh>
    <rbbody  :gettext="gettext"    :isgoto="isgoto" :errvalue="errvalue"></rbbody>
  </div>
</template>

<script type="text/ecmascript-6">
  import rh from  "../components/regist/rh.vue";
  import rbbody from "../components/regist/rbbody.vue";
  import api  from "../../apis/pagapi";
  export default {
    data (){
        return {
          up : {
            u: "",
            p: ""
          }
          ,
          errvalue : {
            isture : false,
            val : ""
          }
        }
    }
    ,
    methods : {
          // 获取文本框的值
          gettext(u,p){
            this.up.u = u;
            this.up.p = p;
          }
          ,
      // 验证系统   验证 账号 密码  图片
        _vbup(){
          var u = this.up.u.length;
          var p = this.up.p.length;

  //                验证账号
          if(u==""){
            this.errvalue.val = "账号不能为空"
            return false;
          }else if(u>11){
            this.errvalue.val = "账号不能超过11个字符"
            return false;
          }
  //                 密码验证
          if(p==""){
            this.errvalue.val = "密码不能为空"
            return false;
          }else if(p>6){
            this.errvalue.val = "密码不能超过6个字符"
            return false;
          }

          return true;
        }
        ,
      //是否调用错误提示
      errval : function (a) {
        var tant = this;
        if(a=="1"){
          this.errvalue.isture = true;
          setTimeout(function () {
            tant.errvalue.isture = false;
          },1600);
        }else{
          this.errvalue.isture = false ;
        }
      }
      ,
      //是否跳转按钮
      isgoto : function () {
        //验证。。。。。
        var a = this._vbup();
        var tant = this;
        if(a===true){
          console.log("2个验证通过,判断用户存在性");
          var url = `/regist?user=${this.up.u}&pass=${this.up.p}`;
          api.getpagapi(url,function (data) {
                  console.log(data)
                  if(data.regist==1){
                    //跳转
                    tant.errvalue.val = "恭喜您，注册成功 ，2秒之后跳转到登陆页面";
                    tant.errval("1");
                    setTimeout(function () {
                          location.href = "/#/login";
                    },2000)
                  }
                  else if(data.regist==2){
                    //调用请求模块
                    tant.errvalue.val = "用户名已经存在，换个吧";
                    tant.errval("1");
                  }else if(data.regist==3){
                    //调用请求模块
                    tant.errvalue.val = "服务器犯病了";
                    tant.errval("1");
                  }else if(data.regist==4){
                    //未知错误
                    tant.errvalue.val = "未知错误";
                    tant.errval("1");
                  }else{
                    tant.errvalue.val = "别学H5了，我偷电瓶车养你";
                    tant.errval("1");
                  }
          });

        }else{
          //调用提示系统
          console.log("有错误，调用 提示模块 ~")
          this.errval("1");
        }
        //防止settimeout
        return;
      }
      ,
      //发送请求模块
      vgo : function (urlname,u,p,cb) {
              var url = `/${urlname}?user=${u}&pass=${p}`;
              api.getpagapi(url,function (data) {
                        cb(data);
              });
      }

    }
    ,
    components : {
      rh,
      rbbody
    }
  }
</script>

<style scoped>

</style>



