<template>
  <div class="lt_body">
      <form action="">
           <b-input :gettext="gettext"></b-input>
           <b-verification  :lists="lists" :imgc="imgc" :changeimg="changeimg"></b-verification>
           <b-btn :isgoto="isgoto"></b-btn>
            <b-zc></b-zc>
            <b-prompt :errvalue="errvalue"></b-prompt>
      </form>
  </div>
</template>

<script type="text/ecmascript-6">
    import  bInput from  "./bInput.vue";
    import  bVerification from  "./bVerification.vue";
    import  bBtn from  "./bBtn.vue";
    import  bZc from  "./bZc.vue";
    import  bPrompt from  "./bPrompt.vue";


    export default {
      props : ["lists","imgc","isgoto","gettext","errvalue","changeimg"],
        components :  {
            bInput ,
            bVerification ,
            bBtn ,
            bZc,
            bPrompt
        }
    }
</script>

<style scoped>
  .lt_body {
    padding: 0 0.27624rem; }
</style>



