<template>
  <div class="lt_body">
      <form action="">
           <rb-input :gettext="gettext"></rb-input>
           <rb-verification></rb-verification>
           <rb-btn :isgoto="isgoto"></rb-btn>
           <b-prompt :errvalue="errvalue"></b-prompt>
      </form>
  </div>
</template>

<script type="text/ecmascript-6">
    import  rbInput from "./rbInput.vue";
    import  rbVerification from "./rbVerification.vue";
    import  rbBtn from "./rbBtn.vue";
    import  bPrompt from "./bPrompt.vue";


    export default {
        props : ["gettext","isgoto","errvalue"],
        components :  {
            rbInput ,
            rbVerification ,
            rbBtn,
            bPrompt
        }
    }
</script>

<style scoped>
  .lt_body {
    padding: 0 0.27624rem; }
</style>



