<template>
    <div class="lt_body_input">
      <div>
        <input v-model.trim ="user"      type="text" value="" placeholder="用户名/邮箱/手机号"/>
        <svg @click="clearnone(1)"  v-show ="err.i1" viewBox="0 0 1024 1024" version="1.1" class="base-field-icon"><path fill="#ccc" d="M512.697383 63.444984c-247.538793 0-448.208115 200.669322-448.208115 448.208115 0 247.539816 200.669322 448.208115 448.208115 448.208115 247.539816 0 448.208115-200.668299 448.208115-448.208115C960.905498 264.114307 760.237199 63.444984 512.697383 63.444984zM690.686147 645.207861c12.265348 12.273535 12.265348 32.1707 0 44.426839-12.282744 12.273535-32.1707 12.273535-44.453445 0l-133.316332-133.316332-133.326565 133.316332c-12.273535 12.273535-32.161491 12.273535-44.445258 0-12.265348-12.256138-12.265348-32.153304 0-44.426839l133.334751-133.353171L335.144548 378.537336c-12.265348-12.256138-12.265348-32.1707 0-44.444235 12.282744-12.256138 32.1707-12.256138 44.445258 0l133.326565 133.334751 133.316332-133.334751c12.282744-12.256138 32.1707-12.256138 44.453445 0 12.265348 12.273535 12.265348 32.18912 0 44.444235L557.351396 511.854691 690.686147 645.207861z"></path></svg>
      </div>
      <div>
        <input v-model.trim ="pass"     type="password" value="" placeholder="密码"/>
        <svg @click="clearnone(2)" v-show ="err.i2" viewBox="0 0 1024 1024" version="1.1" class="base-field-icon"><path fill="#ccc" d="M512.697383 63.444984c-247.538793 0-448.208115 200.669322-448.208115 448.208115 0 247.539816 200.669322 448.208115 448.208115 448.208115 247.539816 0 448.208115-200.668299 448.208115-448.208115C960.905498 264.114307 760.237199 63.444984 512.697383 63.444984zM690.686147 645.207861c12.265348 12.273535 12.265348 32.1707 0 44.426839-12.282744 12.273535-32.1707 12.273535-44.453445 0l-133.316332-133.316332-133.326565 133.316332c-12.273535 12.273535-32.161491 12.273535-44.445258 0-12.265348-12.256138-12.265348-32.153304 0-44.426839l133.334751-133.353171L335.144548 378.537336c-12.265348-12.256138-12.265348-32.1707 0-44.444235 12.282744-12.256138 32.1707-12.256138 44.445258 0l133.326565 133.334751 133.316332-133.334751c12.282744-12.256138 32.1707-12.256138 44.453445 0 12.265348 12.273535 12.265348 32.18912 0 44.444235L557.351396 511.854691 690.686147 645.207861z"></path></svg>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
      data(){
         return {
                user : "",
                pass : "",
                err : {
                 i1  :  false,
                 i2 :  false
               }
         }

      }
      ,
      watch :  {
              user (){
                    if(this.user !=""){
                      this.err.i1 = true;
                    }else{
                      this.err.i1 = false;
                    }
                this.gettext(this.user,this.pass);
              },
              pass(){
                    if(this.pass !=""){
                      this.err.i2 = true;
                    }else{
                      this.err.i2 = false;
                    }
//                  父组件给子组件的值，或者方法   通过this就可以触发
                    this.gettext(this.user,this.pass);
              }
      }
      ,
      mounted : function () {
      }
      ,
      methods :  {
//            清空文本框
            clearnone  : function (i) {
                    if(i==1){
                          this.user = "";
                    }
                    if(i==2){
                        this.pass = "";
                    }
            }
      }
      ,
      props : ["gettext"]
    }
</script>

<style scoped>
  .lt_body .lt_body_input {
    margin: 0.55249rem 0 0.33149rem;
    display: flex;
    flex-flow: column nowrap; }
  .lt_body .lt_body_input > div {
    display: flex;
    align-items: center;
    font-size: 0.22099rem;
    font-family: "微软雅黑";
    color: #333;
    padding: 0.1105rem 0;
    line-height: 1.5;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    position: relative; }
  .lt_body .lt_body_input > div > input {
    vertical-align: middle;
    outline: 0;
    color: #424242;
    -webkit-appearance: none;
    -webkit-user-select: text;
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1 !important;
    -moz-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: inherit;
    color: inherit;
    line-height: 1.5;
    padding: 0 0.0884rem;
    position: relative;
    z-index: 1;
    background-color: transparent; }
  .lt_body .lt_body_input > div .base-field-icon {
    font-size: 0.22099rem;
    width: 0.16575rem;
    height: 0.16575rem;
    vertical-align: middle; }
</style>



