import Vue from 'vue';
import Router from 'vue-router';

import loginnav from '../pages/loginnav.vue';
import login from '../pages/login.vue';
import regist from '../pages/regist.vue';
import exemptLogin from '../pages/exemptLogin.vue';
import sousuo from '../pages/sousuo.vue';

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    { path: '/', redirect: '/loginnav' },
    {path: '/loginnav', name: 'loginnav', component:loginnav},
    {path: '/login', name: 'login', component:login},
    {path: '/regist', name: 'regist', component:regist},
    {path: '/exemptLogin', name: 'exemptLogin', component:exemptLogin},
    {path: '/sousuo', name: 'sousuo', component:sousuo}
  ]
})




