<template>
            <div class="suosuo">
                  <sou-suo-header :sshide="sshide" :sssave="sssave" ></sou-suo-header>
                  <sou-suo-body   :historydata="historydata" :ishistory="ishistory" :ins="ins"></sou-suo-body>
            </div>
</template>

<script type="text/ecmascript-6">
  import  SouSuoHeader from "./SouSuoHeader.vue"
  import  SouSuoBody  from "./SouSuoBody.vue"
    export default {
    props : ["sshide","historydata","ishistory","sssave","ins"],
            components  : {
                  SouSuoHeader,
                  SouSuoBody
            }
    }
</script>

<style scoped>
  .suosuo {
    width: 100%;
    height: 736px;
    position: fixed;
    top: 0;
    left: 0;
    font: 13px/1.5 arial;
    z-index: 9999999;
    background-color: #fff; }

</style>



