<template>
  <div>
    <h></h>
    <bbody :errvalnone="errvalnone" :getsecodbtn="getsecodbtn" :secod="secod" :gettext="gettext"    :isgoto="isgoto" :errvalue="errvalue"></bbody>
  </div>
</template>

<script type="text/ecmascript-6">
  import h from  "../components/exemptLogin/h.vue";
  import bbody from "../components/exemptLogin/bbody.vue";
  import api  from "../../apis/pagapi";
  import  "../assets/js/jquery-2.1.0"
  import "../assets/js/jq.cookie"
  export default {
    data (){
      return {
        up : {
          u: "",
          p: ""
        }
        ,
        errvalue : {
          isture : false,
          val : ""
        }
        ,
        secod : {
          secod : 60,
          secodval : "",
          istrue : false,
          isnull : true
        }
        ,
        secodbval : ""
      }
    }
    ,
    methods : {
      // 获取文本框的值
      gettext(u,p){
        this.up.u = u;
        this.up.p = p;
      }
      ,
      // 验证系统   验证 账号 密码  图片
      _vbup(){
        var u = this.up.u.length;
        var p = this.up.p;

        //                验证账号
        if(u==""){
          this.errvalue.val = "账号不能为空"
          return false;
        }else if(u>11){
          this.errvalue.val = "账号不能超过11个字符"
          return false;
        }
        //                 密码验证
        if(p!=this.secod.secodval){
          this.errvalue.val = "验证码输入不正确"
          return false;
        }
        if(p.length==0){
          this.errvalue.val = "验证码不能为空"
          return false;
        }

        return true;
      }
      ,
      //是否调用错误提示
      errval : function (a,time) {
        var tant = this;
        var timd = time || 1600;
        console.log(timd);
        if(a=="1"){
          this.errvalue.isture = true;
          setTimeout(function () {
            tant.errvalue.isture = false;
            tant.errvalue.val = "";
          },timd);
        }else{
          this.errvalue.isture = false ;
        }
      }
      ,
      //是否跳转按钮
      isgoto : function () {
        //验证。。。。。
        var a = this._vbup();
        var tant = this;
        if(a===true){
          //跳转
          console.log("2个验证通过，发送请求。。。")
          var url = `/exemptLogin?user=${this.up.u}&pass=${this.up.p}`;
          api.getpagapi(url,function (data) {
            if(data.exemptLogin == 1){
              tant.errvalue.val = "恭喜您，注册成功 ，2秒之后跳转到首页";
              tant.errval("1");
              setcookie('username',tant.up.u, { expires: 7 });
//                                      setTimeout(function () {
//                                        location.href = "/#/index";
//                                      },2000)
            }
            else if(data.exemptLogin == 2){
              tant.errvalue.val = "用户名不存在";
              tant.errval("1");
            }
            else {
              tant.errvalue.val = "未知错误";
              tant.errval("1");
            }
          })

        }else{
          //调用提示系统
          console.log("有错误，调用 提示模块 ~")
          this.errval("1");
        }
        //防止settimeout
        return;
      }
    ,
    //获取一个随机的6位数字验证码
    getsecod : function () {
          var val = "";
          for(let i=0;i<6;i++){
                var randomnum  = Number(parseInt(Math.random()*9));
                val += randomnum;
          }
          return val;
    }
    ,

    //获取验证码
      getsecodbtn : function () {
        var a = this.getsecod();
        this.secod.secodval = a;

        this.errvalue.val =`您的验证码为  ${a}  ,有效期为60秒，请尽快输入`
        this.errval("1",5000);


        this.secod.istrue = true;
        this.secod.isnull = false;
        var c = null;
        var tant = this;
        c = setInterval(function () {
              tant.secod.secod--;
              if(tant.secod.secod<=0){
                  tant.secod.secodval = "";
                  tant.secod.istrue = false;
                  tant.secod.isnull = true;
                  tant.secod.secod = 60;
                   clearInterval(c);
                  return;
              }
        },1000)
      }
      ,
//      点击验证模块使其消失
      errvalnone() {
            this.errvalue.isture = false;
      }
    }
    ,
    components : {
      h,
      bbody
    }

  }
</script>

<style scoped>

</style>



