<template>
    <div class="lt_body_verification">
        <p class="pw"><span class="span1">请点击图片旋转至正向朝上</span><a class="span2" @click="changeimg">换一组</a></p>
        <div class="imgbox">
            <b-verimg v-for="(i,index) in lists" :index="index" :item="i" :imgc="imgc"></b-verimg>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import  bVerimg from "./bVerimg.vue"
    export default {
           props : ["lists","imgc","changeimg"],
            components : {
                  bVerimg
            }
    }
</script>

<style scoped>
  .span1{
    color: #666;
    float: left;
    font-size: 12px;
  }
  .span2{
    float: right;
    font-size: 12px;
    color: #999;
  }
  .imgbox{
    overflow: hidden;
    clear: both;
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    text-align: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
    cursor: pointer;
    transition: all ease 0.2s;
    margin-bottom: .166rem;
  }
  .imgbox1{
    width:24.25%;
    height: 74px;
    margin-right: 1%;
  }
.pw{
  overflow: hidden;
  margin-bottom: 8px;
}
</style>



